    font-size: 1.4rem;
    
    /* -webkit-text-stroke: 1px rgb(68, 69, 70); */
    
}
body{
    padding: 0;
    margin: 0;
    
}
.mr-1{
 margin-right: .3rem;
}
.background{
 background:#068f94;
 background-size:100%;
 width: 100%;
 height: -webkit-max-content;
 height: max-content;
 border-radius: .5rem;
 padding: 1rem;
 margin: 1rem;
 transition: ease-in 1s;
 
}
