.recipe-list{
 
 min-height: 100vh;
 width: 100%;
 padding: 1rem;
 transition: all ease-in 2s ;
}
.recipe-list__add-recipe-btn-container{
 text-align: center;
 padding: 1rem 2rem 3rem 2rem;
}
.recipe-list__search-bar-form {
    width: 100%;
   text-align: center;
   padding: 2rem 1rem 2rem 1rem;
    

}
.row{

    --bs-gutter-x: 0rem;
}
.col .recipe-list{
   
    border-right: 1px solid rgb(197, 193, 193);
}


input.recipe-list__search-bar{
    width: 100%;
    max-width: 12rem;
    font-size: inherit;
    padding: .5rem .5rem;
    border: 5px solid rgba(128, 128, 128,0.5);
    border-radius: .2rem;
    text-align: center;
    margin: .5rem;
    outline: none;
    }

.visually-hidden {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: -100px;
}
